<template lang="jade">
  .me-page
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    .width-draw-info.scroll-content

      .bank-form(v-if="stepIndex === 0")
        .notice(style="margin-top: .2rem")
          span.title 
          p.content
            |  
            span.text-danger 5
            |   
            span.text-danger {{ times }} 0
            |  
            br
            span.text-danger 
            br
            |  
            span.text-danger 6
            |  

        .form
          .buttons(style="float: right; padding: .2rem 0")
            .ds-button.primary.large(@click="(type = 'bind') &&  stepIndex++") 
            .ds-button.cancel.large(@click=" (type = 'lock') &&  stepIndex++") 

        el-table.header-bold(:data="myBanks" style="margin: .2rem")
          el-table-column(label="")
            template(scope="scope")
              .ds-icon-bank-card(v-bind:class=" [ scope.row.class ] " style="margin: 0")

          el-table-column(prop="cardNo" label="")
          el-table-column(prop="bindTime" label="")
          el-table-column(label="" min-width="60"  align="center")
            template(scope="scope")
              .ds-button.text-button.blue(@click="unbindCard(scope.row)") 


      
      


      .bank-form(v-if="stepIndex === 1 && type === 'bind' ")
        
        .notice(style="margin-top: .2rem")
          span.title 
          p.content
            | 1. 
            span.text-danger   
            | 
            br
            | 2. 
            span.text-danger   
            | 
            br
            | 3. 
            span.text-danger  5 
            | 
            span.text-danger  2 
            | 
            br
            | 4. 


        p.title(style="padding: 0 .18rem 0 .4rem; margin: .2rem 0; color: #333") 
          span.text-blue {{ nickName }} 110
          |  
          span.ds-button.text-button.blue(style="float: right" @click="stepIndex--") {{ '<' }}
        StepTabs(type="text" v-bind:steps = "bs" v-bind:stepIndex="bi")
        
        hr(style="height: 0; border: 0; border-top: 1px solid #ccc; margin: .2rem .4rem 0 .4rem")

        .form(style="margin: .2rem .4rem" v-if="bi === 0")
         
          p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            el-select(v-model="bank")
              el-option(v-for="(b, index) in BANKS" v-bind:label="b.text" v-bind:value="b")

          p.item 
            el-select(v-model="province")
              el-option(v-for="(p, index) in PROVINCES" v-bind:label="p.text" v-bind:value="p")
          
          p.item 
            el-select(v-model="city")
              el-option(v-for="(c, index) in province.citys" v-bind:label="c.text" v-bind:value="c")
          
          p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large
            span(style="font-size: .12rem")  120

          p.item &nbsp;&nbsp;&nbsp;
            input.ds-input.large
            span(style="font-size: .12rem")  120

          p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large
            span(style="font-size: .12rem")  161819

          p.item 
            input.ds-input.large
            span(style="font-size: .12rem")  

          .buttons(style="margin-left: 1rem; padding-top: .05rem")
            .ds-button.primary.large(@click="bi++") 


        .form(style="margin: .2rem .4rem" v-if="bi === 1")

          .item(style="line-height: .5rem") &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            p.banks(style="color: #333")
              span.ds-icon-bank-card(v-bind:class=" [ bank.class ] ")

            p.item {{ province.text }}
            p.item {{ city.text }}
            p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{  }} bankName
            p.item &nbsp;&nbsp;&nbsp;{{ }} 
            p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              span(style="color: #333") {{ }}********3133
         
          .buttons(style="margin-left: .95rem; padding-top: .05rem")
            .ds-button.primary.large(@click="bindNow") 



      .bank-form(v-if="stepIndex === 1 && type === 'unbind' ")
        p.title(style="padding: 0 .18rem 0 .4rem; margin: .2rem 0; color: #333") 
          span.ds-button.text-button.blue(style="float: right" @click="stepIndex--") {{ '<' }}
        StepTabs(type="text" v-bind:steps = "s" v-bind:stepIndex="i")
        
        hr(style="height: 0; border: 0; border-top: 1px solid #ccc; margin: .2rem .4rem 0 .4rem")

        .form(style="margin: .2rem .4rem" v-if="i === 0")
          .item(style="line-height: .5rem") 
            p.banks(style="color: #333")
              span.ds-icon-bank-card(v-bind:class=" [ bank.class ] ")
              | *********0000
          
          p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large
            span(style="font-size: .12rem")  
          
          p.item &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            input.ds-input.large
            span(style="font-size: .12rem")  

          .buttons(style="margin-left: 1.15rem; padding-top: .05rem")
            .ds-button.primary.large(@click="i++") 

        .form(style="margin: .2rem" v-if="i === 1")
          .item(style="line-height: .5rem") 
            p.banks(style="color: #333")
              span.ds-icon-bank-card(v-bind:class=" [ bank.class ] ")
          
          p.item &nbsp;{{ }}********3133
          p.item &nbsp;{{ }}2015-15-12 12:01:56
          p.item &nbsp;
            input.ds-input.large
        
          .buttons(style="margin-left: 0.7rem; padding-top: .05rem")
            .ds-button.primary.large(@click="unbindNow") 


      .bank-form(v-if="stepIndex === 1 && type === 'lock' ")
        .notice(style="margin-top: .2rem")
          span.title 
          p.content
            | 1. 
            span.text-danger 
            br
            | 2. 
            br
            | 
            br
            
            span.text-danger 



        p.title(style="padding: 0 .18rem 0 .4rem; margin: .2rem 0; color: #333") 
          span.text-blue {{ nickName }} 110
          |  
          span.ds-button.text-button.blue(style="float: right" @click="stepIndex--") {{ '<' }}

        hr(style="height: 0; border: 0; border-top: 1px solid #ccc; margin: .2rem .4rem 0 .4rem")

        .form(style="margin: .2rem .4rem" v-if="i === 0")

          .item(style="line-height: .5rem") &nbsp;&nbsp;&nbsp;
            div.banks(style="color: #333; display: inline-block")
              div(v-for=" bank in myBanks ")
                .ds-icon-bank-card(v-bind:class=" [ bank.class ] ") 
                span(style="color: #333")  {{ }}*********0000
          
          
          p.item  
            input.ds-input.large

          .buttons(style="margin-left: .75rem; padding-top: .05rem")
            .ds-button.primary.large(@click="lockNow") 


</template>

<script>
import StepTabs from 'components/StepTabs'
import { BANKS, PROVINCES } from '../../util/static'
import api from '../../http/api'
// import store from '../../store'
export default {
  data () {
    return {
      BANKS: BANKS,
      bank: {},

      PROVINCES: PROVINCES,
      province: {},
      city: {},

      type: 'bind',
      stepIndex: 0,
      s: ['01 ', '02 ', '03 '],
      i: 0,

      bs: ['01 ', '02 ', '03 '],
      bi: 0
    }
  },
  computed: {
    textMoney () {
      return this.money
    }
  },
  created () {
    this.myBanks = [
      {id: 1, class: 'zggsh', cardNo: 112132484244, bindTime: '1990-02-02 12:56:05', text: ''},
      {id: 1, class: 'zggsh', cardNo: 112132484244, bindTime: '1990-02-02 12:56:05', text: ''}
    ]
    this.getUserBankCards()
  },
  methods: {
    bindNow () {
    },
    unbindCard (row) {
      this.$modal.warn({
        content: '',
        target: this.$el,
        ok () {
          this.type = 'unbind'
          this.stepIndex++
          this.bank = row
        },
        O: this
      })
    },
    unbindNow () {
      this.__loading({
        text: '...',
        target: this.$el
      }, 10000)
      setTimeout(() => {
        this.$modal.success({
          target: this.$el,
          content: '',
          close () {
            this.stepIndex = 0
            this.i = 0
          },
          O: this
        })
      }, 10000)
    },
    lockNow () {
      let modal = this.$modal.warn({
        content: '',
        target: this.$el,
        btn: [''],
        ok () {
          modal.type = 'success'
          modal.content = ''
          return false
        },
        O: this
      })
    },
    getUserBankCards () {
      this.$http.get(api.getUserBankCards).then(({data}) => {
        if (data.success === 1) {
        }
      }).catch(rpe => {
      })
    }
  },
  components: {
    StepTabs
  }
}
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .scroll-content
    top TH
  .width-draw-info
    .cashpwd-form
      padding-top .1rem
  
  .notice
    margin 0 .2rem
    padding PWX
    background-color #fffde8
    border 1px solid #d5d09b
    radius()
    .content
      display inline-block
      margin 0
      line-height .25rem
      vertical-align top
  .form
    margin 0 .4rem
    .amount
      font-family Roboto
      font-size 0.5rem
  .banks
    display inline-block
    margin 0
    max-width 90%
    vertical-align top
  .ds-radio-label
    padding 0 .1rem
  .ds-icon-bank-card
    width 167px
    height 40px
    margin .05rem 
    radius()
    display inline-block

    vertical-align middle
  .buttons
    .ds-button:not(:first-child)
      margin-left .1rem
</style>


